import React, { useMemo, useState, useCallback } from 'react'
import styled from 'styled-components'
import { Heading, Card, CardBody, Text, Button, useModal } from 'uikit'
import FlexLayout from 'components/layout/Flex'
import Page from 'components/layout/Page'
import Spacer from 'components/Spacer'
import Value from 'components/Value'
import Label from 'components/Label'
import { useBnbPriceTWAP } from 'hooks/useExitBoardroom'
import useTokenBalance, { useTokenBalanceInAddress } from 'hooks/useTokenBalance'
import { QuoteToken } from 'config/constants/types'
import { getIfo } from 'utils/addressHelpers'
import { useApproveIfo } from 'hooks/useApprove'
import DepositModal from 'views/Farms/components/DepositModal'
import { useSwapIfo } from 'hooks/useStake'
import BigNumber from 'bignumber.js'

const StyledCardsWrapper = styled.div`
  display: flex;
  width: 600px;
  @media (max-width: 768px) {
    width: 100%;
    flex-flow: column nowrap;
    align-items: center;
  }
`

const StyledCardWrapper = styled.div`
  display: flex;
  flex: 1;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 80%;
  }
`

const StyledIfo = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  @media (max-width: 768px) {
    width: 100%;
  }
`
const StyledCardActions = styled.div`
  display: flex;
  justify-content: center;
  margin-top: ${(props) => props.theme.spacing[6]}px;
  width: 100%;
`

const StyledCardHeader = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`

const BnbPage = () => {
  const price = useBnbPriceTWAP()
  const balance = useTokenBalanceInAddress(QuoteToken.SBNB, getIfo())
  const userBalance = useTokenBalance(QuoteToken.SDO)
  const { onSwap } = useSwapIfo()
  const canBuy = useMemo(() => balance.toNumber() > 0 && new Date().getTime() > 1618790400000, [balance])
  const [requestedApproval, setRequestedApproval] = useState(false)
  const { onApprove, isApproved } = useApproveIfo()

  const handleApprove = useCallback(async () => {
    try {
      setRequestedApproval(true)
      await onApprove()
      setRequestedApproval(false)
    } catch (e) {
      console.error(e)
    }
  }, [onApprove])

  const [onPresentDeposit] = useModal(<DepositModal max={userBalance} onConfirm={onSwap} tokenName={QuoteToken.SDO} />)

  return (
    <Page>
      <FlexLayout>
        <Heading as="h1" size="lg" mb="20px" style={{ textAlign: 'center' }}>
           Supra BNB IFO
        </Heading>
        <Heading as="h1" size="md" mb="10px" style={{ textAlign: 'center' }}>
          IFO starts on April 19, 2021 00:00 UTC
        </Heading>
      </FlexLayout>
      <StyledIfo>
        <StyledCardsWrapper>
          <StyledCardWrapper>
            <Card>
              <CardBody>
                <StyledCardHeader>
                  <Value value={price.toFixed(4)} />
                  <Spacer />
                  <Label text="SDO tokens for 1 BNB" />
                </StyledCardHeader>
                <StyledCardActions>
                  {!isApproved ? (
                    <Button disabled={requestedApproval} onClick={handleApprove}>
                      Approve Supra Dollar
                    </Button>
                  ) : (
                    <Button onClick={onPresentDeposit} disabled={!canBuy}>
                      Buy Supra BNB
                    </Button>
                  )}
                </StyledCardActions>
                <Spacer />
                <Text>A discount of 10% will be applied.</Text>
              </CardBody>
            </Card>
          </StyledCardWrapper>
          <Spacer />
          <StyledCardWrapper>
            <Card>
              <CardBody>
                <StyledCardHeader>
                  <Value value={balance.div(new BigNumber(10).pow(18)).toFixed(5)} />
                  <Spacer />
                  <Label text="SBNB Available" />
                </StyledCardHeader>
                <Spacer />
                <Text>IFO will offer 1000 SBNB.</Text>
                <Spacer />
                <Text>New SSO farm with SBNB-BNB pair coming soon.</Text>
                <Spacer />
                <Text>New Boardroom will support use the same Supra Dollar Share to stake and earn Supra BNB.</Text>
              </CardBody>
            </Card>
          </StyledCardWrapper>
        </StyledCardsWrapper>
      </StyledIfo>
    </Page>
  )
}

export default BnbPage
